import './card.css';

import Url from '../../assets/icons/url';

export default function Card({linkInfo, updateClick}){
  return(
    <div className='Card'>
      <h2 className='Card_title'>{linkInfo.title}</h2>
      <p className='Card_note'>
        {linkInfo.note}
      </p>
      <hr className='Card_line'/>
      <ul className='Card_tags'>
        {
          linkInfo.categories.map(item => <li>{item}</li>)
        }
      </ul>
      <a className='Card_button' href={linkInfo.url} target='_blank'>Visit Site <Url className='url_icon'/></a>
    </div>
  )
}