import React from "react";

import Close from "../../assets/icons/Close";

import './form.css';

export default function Form ({addNewLink, handleChange, title, url, note, categories, modalState, changeModalState}){

  return(
    <div className='form_modal' style={modalState ? {display: 'block'} : {display: 'none'}}>  
      <form className='form' onSubmit={addNewLink}>
          <div className='form_close_container' onClick={() => changeModalState(false)}><Close className='form_close'/></div>
          <div>
            <label className='inputLabel'for='title'>Title</label>
            <input className='form_input' name="title" onChange={handleChange} value={title} required/>
            <label className='inputLabel'for='url'>Url</label>
            <input className='form_input' name="url" onChange={handleChange} value={url} required/>
          </div>
          <div>
            <label className='inputLabel'for='note'>Note</label>
            <textarea name="note" onChange={handleChange} value={note} required/>

            <label className='inputLabel'for='category'>Categories</label>
            <ul className='form_categories'>
              {
                categories.map(category => (
                  <li>
                    <input type="checkbox" name="category" id={category.name} value={category.name} onChange={handleChange} />
                    <label for={category.name}>{category.name}</label>
                  </li>
                ))
              }
            </ul>
          </div>
            <button className='form_button' type="submit">ADD</button>
        </form>
    </div>
  )
}