import { useEffect, useState } from 'react';
import UsfLinksServices from './services/useful_links';
import CategoriesServices from './services/categories';

import Navigation from './components/Navigation/Navigation';
import Dropdown from './components/DropDown/Dropdown';
import Form from './components/Form/Form';
import Card from './components/Card/card';

import './App.css';

function App() {

  // State tracking variables for Form submit
  const [title, setTitle] = useState("");
  const [url, setUrl] = useState("");
  const [clicks, setclicks] = useState(0);
  const [note, setNote] = useState("");
  const [tags, setTags] = useState([]);

  // info pulled from the server
  const [links, setLinks] = useState([]);
  const [categories, setCategories] = useState([]);

  // tracking modal state: false = close, true = open
  // default is closed
  const [modalState, setModalState] = useState(false);

  // functions

  // Load data from DB onload
  useEffect(() => {
    UsfLinksServices.getAll()
      .then(allLinks => {
        setLinks(allLinks);
      });
    CategoriesServices.getAll()
      .then(allCategories => {
        setCategories(allCategories);
      });
  }, [])

  // handles changes of multiple fields in the Form
  // Consider creating a custom hook | https://fullstackopen.com/en/part7/custom_hooks
  const handleChange = (event) => {
    switch (event.target.name) {
      case 'title':
        setTitle(event.target.value);
        break;
      case 'url':
        setUrl(event.target.value);
        break;
      case 'note':
        setNote(event.target.value)
        break;
      case 'category':
        // If checked = true, add the item to the tags array
        if (event.target.checked) {
          setTags(tags.concat(event.target.value));
        }
        // If previously checked and then uncheked remove from tags array
        else {
          let newArray = tags.filter(tag => tag !== event.target.value);
          setTags(newArray);
        }
        break;
      default:
        break;
    }
  }

  const addNewLink = (event) => {
    event.preventDefault();

    // don't close modal if a required field is empty or make a api call
    if (title.length > 0 && url.length > 0 && categories.length > 0) {

      // create object
      const newLinkObj = {
        title: title,
        url: url,
        clicks: clicks,
        note: note,
        categories: tags
      }
      UsfLinksServices.create(newLinkObj)
        .then(returnLink => {
          setLinks(links.concat(returnLink));
        })
        .catch(error => {
          alert("error", error.response.data.error);
        })

        setTitle("");
        setUrl("");
        setclicks(false);
        setNote("");
        setTags([]);

        setModalState(false);
    }    
  }

  // Update the DB-clicked when a link is clicked  
  const updateClick = (link) => {
    const updatedLink = link;
    updatedLink.clicks += 1;

    UsfLinksServices.updateLink(updatedLink.id, updatedLink)
      .then(returnLink => {
        setLinks(links.map(link => link.id !== updatedLink.id ? link : returnLink))
      })
      .catch(error => {
        alert("error", error.response.data.error);
      })
  }

  // function to change the modalState in child components
  const changeModalState = (condition) => {
    setModalState(condition);
  }

  // function to make filtered request
  const filterList = (event) => {
    const filterValue = event.target.value;

    UsfLinksServices.filterLinks(filterValue)
    .then(returnLinks => {
      if(returnLinks.length === 0){
        // No data was found
        setLinks([])
      } else{
      setLinks(returnLinks);
      }
    })
    .catch(error => {
      alert('error', error.response.data.error);
    })
  }

  const noDataFound = <div className='noDataFound'>
    <img className='noDataFound_img' src='/images/noDataFound.png' alt='No data found'/>
    <p>No data was found...</p>
  </div>

return (
    <div className="App">
      <div className='App_container'>
        <Navigation changeModalState={changeModalState} setModalState={setModalState} />
        <hr className='App_line'/>
        {/* Drop down menu */}
        <Dropdown filterList={filterList} categories={categories}/>
        <Form
          modalState={modalState}
          addNewLink={addNewLink}
          handleChange={handleChange}
          title={title} url={url} note={note} categories={categories}
          changeModalState={changeModalState}
        />
        <div>
          {links.length === 0 ? <div>{noDataFound}</div> :
            <div className='app_main-grid'>
              {links.map(link => (
                <Card linkInfo={link} updateClick={updateClick}/>
              ))}
            </div>
          }
        </div>
      </div>
    </div>
  );
}

export default App;
