import './dropdown.css';

export default function Dropdown({filterList, categories}){

  let options = <select name='categories' id='category-selected' onChange={filterList}>
    <option>--All--</option>
    {categories.map(category => (
      <option>{category.name}</option>
    ))}
  </select>

  return(
    <div className='dropdown'>
      <label for='category-selected'>Filter By:</label>
      {options}
    </div>
  )
}