import React from "react";
import Add from "../../assets/icons/Add";
import './navigation.css';

export default function Navigation ({changeModalState}){


  return (
    <div className='Navigation'>
      <h1 className='Navigation_logo'>My<span>Links</span></h1>
      <button className='Navigaiton_button' onClick={() => changeModalState(true)}>Add Link <Add className='Navigation_addIcon'/></button>
    </div>
  )
}   